import * as React from 'react';
import styled from '@emotion/styled';
export const Tray = styled.div `
	color: black;
	font-family: Helvetica, Arial;
	padding: 5px;
	width: auto;
	margin: 0px 10px;
	border: solid 1px ${(p) => p.color};
	border-radius: 5px;
	margin-bottom: 2px;
	cursor: pointer;
`;
export const Tray2 = styled.div `
	font-size: 13px;
	height: 35px;
	width: 120px;
	border-radius: 16px;
	border: none;
	box-shadow: 1px 1px 0px 2px rgba (0,0,0,0.3);
	background: rgb(192,255,0);
	cursor: pointer;
`;
export class TrayItemWidget extends React.Component {
    render() {
        return (React.createElement(Tray, { color: this.props.color || "white", draggable: true, onDragStart: (event) => {
                event.dataTransfer.setData('storm-diagram-node', JSON.stringify(this.props.model));
                this.forceUpdate();
            }, className: "tray-item" }, this.props.name));
    }
}
